<template>
  <transition name="slide">
    <header class="header-fix" v-if="$route.name !== 'Visual'">
      <div class="container">
        <router-link to="/" class="hf-logo">Logo</router-link>
        <nav class="hf-gnb">
          <ul>
            <li><router-link to="/about">About</router-link></li>
            <li><router-link to="/work/pub">Work</router-link></li>
            <li><router-link to="/contact">Contact</router-link></li>
            <li><a href="https://github.com/halfmoonkim" target="_blank">GitHub</a></li>
          </ul>
        </nav>
        <button @click="$emit('openSideGnb')" class="hf-side-gnb-btn">Menu</button>
      </div>
    </header>
  </transition>
</template>

<script>
  export default {
    name: 'HeaderFix',
  }
</script>

<style scoped>
  /* Header-Animate */
  .slide-enter-active {
    animation: slide-in 1s ease;
  }

  .slide-leave-active {
    position: fixed;
  }

  @keyframes slide-in {
    from {
      transform: translateX(-100vw);
    }

    to {
      transform: translateX(0);
    }
  }

  /* // Header-Animate */

  /* Header */
  .header-fix {
    display: block;
    width: 100%;
    background-color: #FEFEEB;
    z-index: 10;
  }

  .header-fix .container {
    min-height: 10rem;
  }

  .hf-logo {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    display: block;
    width: 9.5vh;
    min-width: 8rem;
    height: 9.5vh;
    min-height: 8rem;
    background: url('../../images/Profile_logo_fix.png') no-repeat center;
    background-size: contain;
    margin: 0 1rem;
    font-size: 0;
  }

  .hf-gnb {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    margin: 0 1rem;
    text-align: center;
  }

  .hf-gnb>ul>li {
    position: relative;
    display: inline-block;
    padding: 0 1.5vh;
  }

  .hf-gnb>ul>li>a {
    font-family: 'Playfair Display', sans-serif;
    font-size: 1.8rem;
    color: #222C40;
    font-weight: 700;
    line-height: 1.5;
    letter-spacing: 0.5rem;
  }

  .hf-gnb>ul>li::after {
    content: '';
    position: absolute;
    left: 0rem;
    bottom: -1rem;
    width: 100%;
    height: 0.3rem;
    background: #222C40;
    transform: scale(0);
    transition: transform 0.3s;
  }

  .hf-gnb>ul>li:hover::after {
    transform: scale(0.7);
  }

  .hf-side-gnb-btn {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    display: none;
    background: none;
    border: none;
    cursor: pointer;
    margin: 0 1rem;
    font-family: 'Playfair Display', sans-serif;
    font-size: 1.8rem;
    color: #222C40;
    font-weight: 700;
    line-height: 1.5;
    letter-spacing: 0.5rem;
  }

  @media (max-width: 768px) {
    .hf-gnb {
      display: none;
    }

    .hf-side-gnb-btn {
      display: block;
    }
  }

  /* // Header */
</style>