<template>
  <section class="visual">
    <div class="container">
      <h2>Visual</h2>
      <div class="occup-box">
        <p>&#35; Web Publisher</p>
        <p>&#35; Front-End Developer</p>
      </div>
      <div class="visual-box">
        <p class="visual-txt">
          보름달이 되기 위하여 매일 같이 차오르는 반달처럼<br>
          끊임없는 발전을 위해 노력하는 
          <span class="typed-txt">{{ typeValue }}</span>
          <span class="cursor" :class="{'typing': typeStatus}">&nbsp;</span>
          입니다.
        </p>
      </div>
      <p># 이 사이트는 PC와 모바일 환경에 최적화 되어있습니다.</p>
    </div>
  </section>
</template>

<script>
  export default {
    name: 'index',

    data() {
      return {
        typeValue: '',
        typeStatus: false,
        typeArray: ['"김상현"'],
        typingSpeed: 200,
        erasingSpeed: 100,
        newTextDelay: 1500,
        typeArrayIndex: 0,
        charIndex: 0 
      }
    },

    methods: {
      typeText() {
        if(this.charIndex < this.typeArray[this.typeArrayIndex].length) {
          if(!this.typeStatus)
            this.typeStatus = true;

          this.typeValue += this.typeArray[this.typeArrayIndex].charAt(this.charIndex);
          this.charIndex += 1;

          setTimeout(this.typeText, this.typingSpeed);
        } else {
          this.typeStatus = false;
          setTimeout(this.eraseText, this.newTextDelay);
        }
      },
      eraseText() {
        if(this.charIndex > 0) {
          if(!this.typeStatus)
            this.typeStatus = true;

          this.typeValue = this.typeArray[this.typeArrayIndex].substring(0, this.charIndex - 1);
          this.charIndex -= 1;
          setTimeout(this.eraseText, this.erasingSpeed);
        } else {
          this.typeStatus = false;
          this.typeArrayIndex += 1;
          if(this.typeArrayIndex >= this.typeArray.length)
            this.typeArrayIndex = 0;

          setTimeout(this.typeText, this.typingSpeed + 1000);
        }
      }
    },

    created() {
      setTimeout(this.typeText, this.newTextDelay + 200);
    }
  }
</script>

<style scoped>
  /* Visual */
  .visual {
    background: url('../../images/full-moon.png') no-repeat center;
    background-size: contain;
    margin: 0 5vw;
  }

  .visual .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 50vh;
    text-align: center;
  }

  .visual .container h2 {
    font-size: 0;
  }

  .occup-box>p {
    font-family: 'Montserrat', sans-serif;
    font-size: 3.2rem;
    font-weight: 700;
    letter-spacing: 0.25rem;
    text-align: left;
    transition: all 0.5s;
  }

  .visual-box {
    border: 0.7rem solid #FEFEEB;
    padding: 0.15vw 7vw;
    margin-bottom: 2rem;
  }

  .visual-txt {
    font-size: 3.2rem;
    letter-spacing: 0.25rem;
    text-align: left;
  }

  .typed-txt {
    color: #222c40;
    font-weight: 700;
    text-shadow: -0.1rem 0 #FEFEEB, 0 0.1rem #FEFEEB, 0.1rem 0 #FEFEEB, 0 -0.1rem #FEFEEB;
  }

  .cursor {
    display: inline-block;
    margin-left: 0.3rem;
    width: 0.4rem;
    background: transparent;
    animation: cursorBlink 1s infinite;
    line-height: 1;
  }

  .typing {
    animation: none !important;
  }

  @keyframes cursorBlink {
    49% {
      background-color: #FEFEEB;
    }

    50% {
      background-color: transparent;
    }

    99% {
      background-color: transparent;
    }
  }

  @media (max-width: 900px) {
    .occup-box>p {
      font-size: 2.4rem;
    }

    .visual-txt {
      font-size: 2.4rem;
      letter-spacing: 0.2rem;
    }
  }

  @media (max-width: 675px) {
    .occup-box>p {
      font-size: 2rem;
    }

    .visual-txt {
      font-size: 2rem;
    }
  }

  @media (max-width: 559px) {
    .occup-box>p {
      font-size: 1.8rem;
    }

    .visual-txt {
      font-size: 1.8rem;
    }
  }

  @media (max-width: 510px) {
    .container>p {
      font-size: 1.4rem;
      letter-spacing: 0.1rem;
    }

    .occup-box>p {
      font-size: 1.4rem;
    }

    .visual-txt {
      font-size: 1.8rem;
    }
  }

  @media (max-width: 425px) {
    .container>p {
      font-size: 1.2rem;
    }

    .visual-txt {
      font-size: 1.8rem;
      letter-spacing: 0.25rem;
    }
  }

  @media (max-width: 320px) {
    .visual-txt {
      font-size: 1.8rem;
    }
  }

  /* // Visual */
</style>